@import "@/styles/main.scss";




































































































.contact-left-section{
      flex-direction: column;
      width: 50%;
      height: 600px;
      justify-content: space-between;
      align-items: flex-start;
      padding-top: 110px;
      padding-left: 49px;
      z-index: 10;

      .map-link{
        text-decoration: none !important;

        :hover{
          text-decoration: none !important;
        }
      }

      .contact-tooltip{
        font-size: 0.9rem;
        background-color: rgba($color: #222222, $alpha: 0.9);
        border-radius: 8px;
        padding: 4px 20px 4px 8px;
        color: #999999;
        position: relative;
        display: none;

        .icon{
          color: #999999;
          font-size: 0.9rem;
          position: absolute;
          top: 25%;
          right: 8px;

        }

        &:hover{
          background-color: rgba($color: #222222, $alpha: 0.9);
          color: #ffffff;

        }
      }

      .contact-txt{
        font-weight: 500;
        font-size: 3.5rem;
        z-index: 10;
      }
  }

  .contact-right-section{
      width: 50%;
      height: 600px;
      justify-content: flex-end;
      align-items: flex-end;
      padding-right: 40px;

      .modal-contact-page{
        background-color: #ffffff;
        border-radius: 9px;
        padding: 0 0 0 10px;
        cursor: default;
        width: 360px;
        height: 460px;
        margin-bottom: -40px;
        justify-content: flex-start;
        align-items: flex-start;
        -webkit-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.65);
        -moz-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.65);
        box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.65);

        p{
          width: 100%;
          text-align: left;
          padding: 20px 20px 0;
          margin: 0;
          margin-bottom: -5px;
          font-family: 'Helvetica', 'Roboto' !important;
          font-size: 1rem;
          letter-spacing: 0;
          font-weight: 600;
          color: #000000;
        }

        .contact-page-form{
          padding: 30px 20px 10px;

          input{
            width: 100%;
            border: 1px solid #000000;
            border-radius: 5px;
            background-color: transparent;
            margin-bottom: 20px;
            padding: 12px;
            font-size: 14px;

              &:focus{
                  outline: none;
                  border-bottom: 2px solid #000000;

                  &::placeholder{
                      color: #000000 !important;
                      font-weight: 600;
                      -webkit-transform: translateX(10px);
                      -moz-transform: translateX(10px);
                    }
              }

              &::-webkit-input-placeholder{
                  transition: all 0.3s ease;
                  color: #000000 ;
                  font-weight: 500 ;
                  font-size: 1rem;
              }

              &::-ms-input-placeholder{
                  transition: all 0.3s ease;
                  color: #000000 !important;
                  font-weight: 500 !important;
                  font-size: 1rem;
              }
          }

          textarea{
              width: 100%;
              border: 1px solid #000000;
              border-radius: 5px;
              margin-bottom: 45px;
              background-color: transparent;
              padding: 12px;
              font-size: 14px;

              &:focus{
                  outline: none;
                  border-bottom: 2px solid #000000;
                  // animation-name: pulse;
                  // animation-duration: 0.3s;
                  &::placeholder{
                      color: #000000 !important;
                      font-weight: 600;
                      -webkit-transform: translateX(10px);
                      -moz-transform: translateX(10px);
                    }
              }

              &::-webkit-input-placeholder{
                  transition: all 0.3s ease;
                  color: #101010 ;
                  font-weight: 500 ;
                  font-size: 1rem;
              }

              &::-ms-input-placeholder{
                  transition: all 0.3s ease;
                  color: #101010 !important;
                  font-weight: 500 !important;
                  font-size: 1rem;
              }
          }

          .contact-form-buttons{
            justify-content: center;
            align-items: center;
            margin-top: 5px;

            .send-message{
              display: flex;
              justify-content: space-between;
              align-items: center;
              width: 70%;
              height: 50px;
              background-color: transparent;
              color: #000000;
              font-weight: 700;
              letter-spacing: 0.1rem;
              word-spacing: 0.2rem;
              font-size: 1rem;
              padding: 0 25px 0 25px;
              border: 1px solid #000000;
              outline: none;
              border-radius: 10px;
              transition: all 0.2s ease;

              &:hover{
                  background-color: #000000;
                  color: #ffffff !important;

                  svg{
                  color: #ffffff;
                  font-size: 1.1rem;
                  }
              }

                svg{
                  color: #000000;
                  font-size: 1.1rem;
                }

                .loading{
                  color: #000000;
                  font-size: 2rem !important;
                  animation:
                            loading-rotate 1s infinite both;
                            // loading-bg 1.5s infinite both;

                  @keyframes loading-rotate {
                    0% {
                      transform: rotateZ(0deg);
                    }
                    100% {
                      transform: rotateZ(360deg);
                    }
                  }

                  @keyframes loading-bg {
                    0% {
                      color: #ffffff;
                    }
                    100% {
                      color: #000000;
                    }
                  }
              }
            }

          }
        }
      }
  }
